@import url(https://fonts.googleapis.com/css?family=Saira+Stencil+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/static/media/mockup.5bf7a60b.png) no-repeat; /* Firefox 3.6+ */ /* Safari 3.1+ и Chrome 4.0+ */ /* Opera 9.6+ */
  background-size: 100%; /* Современные браузеры */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.application {
  margin-top: 10px;
  margin-left: 80px;
  display: flex;
  align-items: center;
  font-family: 'Saira Stencil One', cursive;
}
.calculator {
  width: 400px;
  height: 500px;
}
.calc-row:last-child {
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
.calc-row {
  display: flex;
  background-color: #000000;
  box-shadow: 10px 10px 20px #000000;
}
.about {
  display: flex;
  color: #000000;
  justify-content: flex-end;
  font-weight: bold;
  margin-bottom: 5px;
  font-size: 20px;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}
.button-box {
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 30px;
    background-color: #E0E1E6;
    color: #000000;
    flex: 1 1;
    border-radius: 40%;
    margin: 3px;
}
.button-box:hover {
    background-color: #ffffff;
    color: #000000;
}
.operator {
    background-color: #FF9600;
    color: #ffffff;
}
.input {
    height: 70px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-weight: bold;
    font-size: 50px;
    background-color: #000000;
    color: #E0E1E6;
    padding: 20px;
    box-shadow: 10px 10px 20px #000000;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
}
.clr {
    margin-top: 5px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 20px;
    background-color: #726F6F;
    color: #000000;
    flex: 1 1;
    border-radius: 20px;
}
.clr:hover {
    background-color: #FFFFFF;
}
